/**
 * Pseudocode 
 *  Cambridge 9618 
 */

class PseudocodeParser {
    constructor() {
        this.reset();
    }

    reset() {
        this.tokens = [];
        this.position = 0;
        this.errors = [];
        this.ast = null;
    }

    parse(tokens) {
        this.reset();
        this.tokens = tokens;
        
        try {
            this.ast = this.parseProgram();
        } catch (error) {
            this.addError(error.message);
        }

        return {
            ast: this.ast,
            errors: this.errors
        };
    }

    // token
    currentToken() {
        return this.position < this.tokens.length ? this.tokens[this.position] : null;
    }

    // token
    peekToken(offset = 1) {
        const pos = this.position + offset;
        return pos < this.tokens.length ? this.tokens[pos] : null;
    }

    // token
    advance() {
        if (this.position < this.tokens.length) {
            this.position++;
        }
    }

    // token
    match(type, value = null) {
        const token = this.currentToken();
        if (!token) return false;
        if (token.type !== type) return false;
        if (value !== null && token.value !== value) return false;
        return true;
    }

    // token
    consume(type, value = null, errorMessage = null) {
        if (this.match(type, value)) {
            const token = this.currentToken();
            this.advance();
            return token;
        }
        
        if (errorMessage) {
            throw new Error(errorMessage);
        }
        
        const message = this.getFriendlyConsumeError(type, value);
        throw new Error(message);
    }

    // 
    parseProgram() {
        const statements = [];
        
        while (this.currentToken() && this.currentToken().type !== 'EOF') {
            try {
                const stmt = this.parseStatement();
                if (stmt) {
                    statements.push(stmt);
                }
            } catch (error) {
                this.addError(error.message);
                this.synchronize();
            }
        }
        
        return {
            type: 'Program',
            statements
        };
    }

    // 
    parseStatement() {
        const token = this.currentToken();
        if (!token) return null;

        // token
        if (token.type === 'KEYWORD') {
            switch (token.value) {
                case 'DECLARE':
                    return this.parseDeclaration();
                case 'CONSTANT':
                    return this.parseConstant();
                case 'TYPE':
                    return this.parseTypeDefinition();
                case 'IF':
                    return this.parseIfStatement();
                case 'FOR':
                    return this.parseForLoop();
                case 'WHILE':
                    return this.parseWhileLoop();
                case 'REPEAT':
                    return this.parseRepeatLoop();
                case 'INPUT':
                    return this.parseInput();
                case 'OUTPUT':
                    return this.parseOutput();
                case 'PROCEDURE':
                    return this.parseProcedure();
                case 'FUNCTION':
                    return this.parseFunction();
                case 'CALL':
                    return this.parseCall();
                case 'RETURN':
                    return this.parseReturn();
                default:
                    this.addError(`${token.line}: : ${token.value}\nDECLAREIFFOR`, token.line);
                    return null;
            }
        } else if (token.type === 'IDENTIFIER') {
            return this.parseAssignment();
        } else {
            const friendlyMessage = this.getFriendlyErrorMessage(token);
            throw new Error(friendlyMessage);
        }
    }

    // 
    parseDeclaration() {
        this.consume('KEYWORD', 'DECLARE');
        
        // 
        const identifiers = [];
        do {
            const identifier = this.consume('IDENTIFIER');
            identifiers.push(identifier);
            
            if (this.match('COMMA')) {
                this.advance();
            } else {
                break;
            }
        } while (true);
        
        this.consume('COLON');
        
        let dataType;
        if (this.match('KEYWORD', 'ARRAY')) {
            dataType = this.parseArrayType();
        } else {
            dataType = this.parseDataType();
        }
        
        // 
        if (identifiers.length === 1) {
            return {
                type: 'Declaration',
                identifier: identifiers[0].value,
                dataType,
                line: identifiers[0].line
            };
        }
        
        // 
        return {
            type: 'MultipleDeclaration',
            identifiers: identifiers.map(id => id.value),
            dataType,
            line: identifiers[0].line
        };
    }

    // 
    parseArrayType() {
        this.consume('KEYWORD', 'ARRAY');
        this.consume('LBRACKET');
        
        const dimensions = [];
        do {
            const lower = this.parseExpression();
            this.consume('COLON');
            const upper = this.parseExpression();
            dimensions.push({ lower, upper });
            
            if (this.match('COMMA')) {
                this.advance();
            } else {
                break;
            }
        } while (true);
        
        this.consume('RBRACKET');
        this.consume('KEYWORD', 'OF');
        const elementType = this.parseDataType();
        
        return {
            type: 'ArrayType',
            dimensions,
            elementType
        };
    }

    // 
    parseDataType() {
        const validTypes = ['INTEGER', 'REAL', 'CHAR', 'STRING', 'BOOLEAN', 'DATE'];
        const token = this.currentToken();
        
        if (token && token.type === 'KEYWORD' && validTypes.includes(token.value)) {
            this.advance();
            return {
                type: 'DataType',
                value: token.value
            };
        }
        
        if (token && token.type === 'IDENTIFIER') {
            this.advance();
            return {
                type: 'UserDefinedType',
                value: token.value
            };
        }
        
        throw new Error('');
    }

    // 
    parseConstant() {
        this.consume('KEYWORD', 'CONSTANT');
        const identifier = this.consume('IDENTIFIER');
        this.consume('EQUALS');
        const value = this.parseExpression();
        
        return {
            type: 'Constant',
            identifier: identifier.value,
            value,
            line: identifier.line
        };
    }

    // 
    parseTypeDefinition() {
        this.consume('KEYWORD', 'TYPE');
        const identifier = this.consume('IDENTIFIER');
        
        const fields = [];
        while (!this.match('KEYWORD', 'ENDTYPE')) {
            if (this.match('KEYWORD', 'DECLARE')) {
                this.advance();
                const fieldName = this.consume('IDENTIFIER');
                this.consume('COLON');
                
                let fieldType;
                if (this.match('KEYWORD', 'ARRAY')) {
                    fieldType = this.parseArrayType();
                } else {
                    fieldType = this.parseDataType();
                }
                
                fields.push({
                    name: fieldName.value,
                    type: fieldType,
                    line: fieldName.line
                });
            } else {
                throw new Error('TYPEDECLARE');
            }
        }
        
        this.consume('KEYWORD', 'ENDTYPE');
        
        return {
            type: 'TypeDefinition',
            identifier: identifier.value,
            fields,
            line: identifier.line
        };
    }

    // 
    parseAssignment() {
        const left = this.parseLeftHandSide();
        this.consume('ASSIGN');
        const right = this.parseExpression();
        
        return {
            type: 'Assignment',
            left,
            right,
            line: left.line
        };
    }

    // 
    parseLeftHandSide() {
        const identifier = this.consume('IDENTIFIER');
        let node = {
            type: 'Identifier',
            value: identifier.value,
            line: identifier.line
        };
        
        // 
        if (this.match('LPAREN')) {
            this.advance();
            const args = this.parseArgumentList();
            this.consume('RPAREN');
            
            node = {
                type: 'FunctionCall',
                name: identifier.value,
                arguments: args,
                line: identifier.line
            };
        }
        
        // 
        while (this.match('LBRACKET')) {
            this.advance();
            const indices = [];
            
            do {
                indices.push(this.parseExpression());
                if (this.match('COMMA')) {
                    this.advance();
                } else {
                    break;
                }
            } while (true);
            
            this.consume('RBRACKET');
            
            node = {
                type: 'ArrayAccess',
                array: node,
                indices,
                line: identifier.line
            };
        }
        
        // 
        while (this.match('DOT')) {
            this.advance();
            const field = this.consume('IDENTIFIER');
            
            node = {
                type: 'FieldAccess',
                object: node,
                field: field.value,
                line: identifier.line
            };
        }
        
        return node;
    }

    // IF
    parseIfStatement() {
        this.consume('KEYWORD', 'IF');
        const condition = this.parseExpression();
        this.consume('KEYWORD', 'THEN');
        
        const thenStatements = [];
        while (!this.match('KEYWORD', 'ELSE') && 
               !this.match('KEYWORD', 'ELSEIF') && 
               !this.match('KEYWORD', 'ENDIF')) {
            thenStatements.push(this.parseStatement());
        }
        
        const elseIfClauses = [];
        while (this.match('KEYWORD', 'ELSEIF')) {
            this.advance();
            const elseIfCondition = this.parseExpression();
            this.consume('KEYWORD', 'THEN');
            
            const elseIfStatements = [];
            while (!this.match('KEYWORD', 'ELSE') && 
                   !this.match('KEYWORD', 'ELSEIF') && 
                   !this.match('KEYWORD', 'ENDIF')) {
                elseIfStatements.push(this.parseStatement());
            }
            
            elseIfClauses.push({
                condition: elseIfCondition,
                statements: elseIfStatements
            });
        }
        
        let elseStatements = [];
        if (this.match('KEYWORD', 'ELSE')) {
            this.advance();
            while (!this.match('KEYWORD', 'ENDIF')) {
                elseStatements.push(this.parseStatement());
            }
        }
        
        this.consume('KEYWORD', 'ENDIF');
        
        return {
            type: 'IfStatement',
            condition,
            thenStatements,
            elseIfClauses,
            elseStatements
        };
    }

    // FOR
    parseForLoop() {
        this.consume('KEYWORD', 'FOR');
        const variable = this.consume('IDENTIFIER');
        this.consume('ASSIGN');
        const start = this.parseExpression();
        this.consume('KEYWORD', 'TO');
        const end = this.parseExpression();
        
        let step = null;
        if (this.match('KEYWORD', 'STEP')) {
            this.advance();
            step = this.parseExpression();
        }
        
        const statements = [];
        while (!this.match('KEYWORD', 'NEXT')) {
            statements.push(this.parseStatement());
        }
        
        this.consume('KEYWORD', 'NEXT');
        this.consume('IDENTIFIER', variable.value);
        
        return {
            type: 'ForLoop',
            variable: variable.value,
            start,
            end,
            step,
            statements
        };
    }

    // WHILE
    parseWhileLoop() {
        this.consume('KEYWORD', 'WHILE');
        const condition = this.parseExpression();
        
        const statements = [];
        while (!this.match('KEYWORD', 'ENDWHILE')) {
            statements.push(this.parseStatement());
        }
        
        this.consume('KEYWORD', 'ENDWHILE');
        
        return {
            type: 'WhileLoop',
            condition,
            statements
        };
    }

    // REPEAT
    parseRepeatLoop() {
        this.consume('KEYWORD', 'REPEAT');
        
        const statements = [];
        while (!this.match('KEYWORD', 'UNTIL')) {
            statements.push(this.parseStatement());
        }
        
        this.consume('KEYWORD', 'UNTIL');
        const condition = this.parseExpression();
        
        return {
            type: 'RepeatLoop',
            statements,
            condition
        };
    }

    // INPUT
    parseInput() {
        this.consume('KEYWORD', 'INPUT');
        
        let prompt = null;
        let variable;
        
        if (this.match('STRING_LITERAL')) {
            prompt = this.currentToken().value;
            this.advance();
            this.consume('COMMA');
        }
        
        variable = this.parseLeftHandSide();
        
        return {
            type: 'Input',
            prompt,
            variable
        };
    }

    // OUTPUT
    parseOutput() {
        this.consume('KEYWORD', 'OUTPUT');
        
        const expressions = [];
        do {
            expressions.push(this.parseExpression());
            if (this.match('COMMA')) {
                this.advance();
            } else {
                break;
            }
        } while (true);
        
        return {
            type: 'Output',
            expressions
        };
    }

    // 
    parseProcedure() {
        this.consume('KEYWORD', 'PROCEDURE');
        const name = this.consume('IDENTIFIER');
        
        this.consume('LPAREN');
        const parameters = this.parseParameterList();
        this.consume('RPAREN');
        
        const statements = [];
        while (!this.match('KEYWORD', 'ENDPROCEDURE')) {
            statements.push(this.parseStatement());
        }
        
        this.consume('KEYWORD', 'ENDPROCEDURE');
        
        return {
            type: 'Procedure',
            name: name.value,
            parameters,
            statements
        };
    }

    // 
    parseFunction() {
        this.consume('KEYWORD', 'FUNCTION');
        const name = this.consume('IDENTIFIER');
        
        this.consume('LPAREN');
        const parameters = this.parseParameterList();
        this.consume('RPAREN');
        
        this.consume('KEYWORD', 'RETURNS');
        const returnType = this.parseDataType();
        
        const statements = [];
        while (!this.match('KEYWORD', 'ENDFUNCTION')) {
            statements.push(this.parseStatement());
        }
        
        this.consume('KEYWORD', 'ENDFUNCTION');
        
        return {
            type: 'Function',
            name: name.value,
            parameters,
            returnType,
            statements
        };
    }

    // 
    parseParameterList() {
        const parameters = [];
        
        if (!this.match('RPAREN')) {
            do {
                let passBy = 'BYVAL'; // 
                if (this.match('KEYWORD', 'BYREF') || this.match('KEYWORD', 'BYVAL')) {
                    passBy = this.currentToken().value;
                    this.advance();
                }
                
                const name = this.consume('IDENTIFIER');
                this.consume('COLON');
                const dataType = this.parseDataType();
                
                parameters.push({
                    name: name.value,
                    dataType,
                    passBy
                });
                
                if (this.match('COMMA')) {
                    this.advance();
                } else {
                    break;
                }
            } while (true);
        }
        
        return parameters;
    }

    // /
    parseCall() {
        this.consume('KEYWORD', 'CALL');
        const name = this.consume('IDENTIFIER');
        
        this.consume('LPAREN');
        const args = this.parseArgumentList();
        this.consume('RPAREN');
        
        return {
            type: 'Call',
            name: name.value,
            arguments: args
        };
    }

    // 
    parseArgumentList() {
        const args = [];
        
        if (!this.match('RPAREN')) {
            do {
                args.push(this.parseExpression());
                if (this.match('COMMA')) {
                    this.advance();
                } else {
                    break;
                }
            } while (true);
        }
        
        return args;
    }

    // RETURN
    parseReturn() {
        this.consume('KEYWORD', 'RETURN');
        let value = null;
        
        if (!this.isStatementEnd()) {
            value = this.parseExpression();
        }
        
        return {
            type: 'Return',
            value
        };
    }

    // 
    parseExpression() {
        return this.parseLogicalOr();
    }

    // 
    parseLogicalOr() {
        let left = this.parseLogicalAnd();
        
        while (this.match('KEYWORD', 'OR')) {
            const operator = this.currentToken().value;
            this.advance();
            const right = this.parseLogicalAnd();
            
            left = {
                type: 'BinaryExpression',
                operator,
                left,
                right
            };
        }
        
        return left;
    }

    // 
    parseLogicalAnd() {
        let left = this.parseEquality();
        
        while (this.match('KEYWORD', 'AND')) {
            const operator = this.currentToken().value;
            this.advance();
            const right = this.parseEquality();
            
            left = {
                type: 'BinaryExpression',
                operator,
                left,
                right
            };
        }
        
        return left;
    }

    // 
    parseEquality() {
        let left = this.parseRelational();
        
        while (this.match('EQUALS') || this.match('NOT_EQUALS')) {
            const operator = this.currentToken().type;
            this.advance();
            const right = this.parseRelational();
            
            left = {
                type: 'BinaryExpression',
                operator,
                left,
                right
            };
        }
        
        return left;
    }

    // 
    parseRelational() {
        let left = this.parseAdditive();
        
        while (this.match('LESS_THAN') || this.match('GREATER_THAN') || 
               this.match('LESS_EQUAL') || this.match('GREATER_EQUAL')) {
            const operator = this.currentToken().type;
            this.advance();
            const right = this.parseAdditive();
            
            left = {
                type: 'BinaryExpression',
                operator,
                left,
                right
            };
        }
        
        return left;
    }

    // 
    parseAdditive() {
        let left = this.parseMultiplicative();
        
        while (this.match('PLUS') || this.match('MINUS')) {
            const operator = this.currentToken().type;
            this.advance();
            const right = this.parseMultiplicative();
            
            left = {
                type: 'BinaryExpression',
                operator,
                left,
                right
            };
        }
        
        return left;
    }

    // 
    parseMultiplicative() {
        let left = this.parseUnary();
        
        while (this.match('MULTIPLY') || this.match('DIVIDE') || 
               this.match('KEYWORD', 'DIV') || this.match('KEYWORD', 'MOD')) {
            const operator = this.currentToken().value || this.currentToken().type;
            this.advance();
            const right = this.parseUnary();
            
            left = {
                type: 'BinaryExpression',
                operator,
                left,
                right
            };
        }
        
        return left;
    }

    // 
    parseUnary() {
        if (this.match('KEYWORD', 'NOT') || this.match('MINUS')) {
            const operator = this.currentToken().value || this.currentToken().type;
            this.advance();
            const operand = this.parseUnary();
            
            return {
                type: 'UnaryExpression',
                operator,
                operand
            };
        }
        
        return this.parsePrimary();
    }

    // 
    parsePrimary() {
        const token = this.currentToken();
        
        if (!token) {
            throw new Error('');
        }
        
        // 
        if (token.type === 'INTEGER_LITERAL' || 
            token.type === 'REAL_LITERAL' || 
            token.type === 'STRING_LITERAL' || 
            token.type === 'CHAR_LITERAL') {
            this.advance();
            return {
                type: 'Literal',
                dataType: token.type,
                value: token.value
            };
        }
        
        // 
        if (token.type === 'KEYWORD' && (token.value === 'TRUE' || token.value === 'FALSE')) {
            this.advance();
            return {
                type: 'Literal',
                dataType: 'BOOLEAN_LITERAL',
                value: token.value
            };
        }
        
        // 
        if (token.type === 'LPAREN') {
            this.advance();
            const expr = this.parseExpression();
            this.consume('RPAREN');
            return expr;
        }
        
        // 
        if (token.type === 'IDENTIFIER') {
            return this.parseLeftHandSide();
        }
        
        // ASCCHRINTRAND
        if (token.type === 'KEYWORD' && 
            ['ASC', 'CHR', 'INT', 'RAND', 'LENGTH', 'SUBSTRING', 'LEFT', 'RIGHT', 'MID', 'UCASE', 'LCASE'].includes(token.value)) {
            const functionName = token.value;
            this.advance();
            
            // 
            if (this.match('LPAREN')) {
                this.advance();
                const args = this.parseArgumentList();
                this.consume('RPAREN');
                
                return {
                    type: 'FunctionCall',
                    name: functionName,
                    arguments: args,
                    line: token.line
                };
            } else {
                throw new Error(` ${functionName} `);
            }
        }
        
        throw new Error(`token: ${token.type}(${token.value})`);
    }

    // 
    isStatementEnd() {
        const token = this.currentToken();
        if (!token || token.type === 'EOF') return true;
        
        const endKeywords = [
            'ENDIF', 'ENDWHILE', 'ENDPROCEDURE', 'ENDFUNCTION',
            'NEXT', 'UNTIL', 'ELSE', 'ELSEIF', 'OTHERWISE'
        ];
        
        return token.type === 'KEYWORD' && endKeywords.includes(token.value);
    }

    // 
    synchronize() {
        this.advance();
        
        while (this.currentToken() && this.currentToken().type !== 'EOF') {
            const token = this.currentToken();
            
            if (token.type === 'KEYWORD') {
                const syncKeywords = [
                    'DECLARE', 'CONSTANT', 'IF', 'FOR', 'WHILE', 'REPEAT',
                    'INPUT', 'OUTPUT', 'PROCEDURE', 'FUNCTION', 'CALL', 'RETURN'
                ];
                
                if (syncKeywords.includes(token.value)) {
                    return;
                }
            }
            
            this.advance();
        }
    }

    // consume
    getFriendlyConsumeError(expectedType, expectedValue = null) {
        const token = this.currentToken();
        const line = token ? token.line : '';
        
        if (expectedType === 'COLON') {
            // 
            const currentTokenValue = token ? token.value : '';
            if (currentTokenValue === 'RETURNS' || currentTokenValue === 'REAL' || currentTokenValue === 'INTEGER' || 
                currentTokenValue === 'STRING' || currentTokenValue === 'BOOLEAN' || currentTokenValue === 'CHAR' || currentTokenValue === 'DATE') {
                return `${line}: \n FUNCTION name(param : TYPE) RETURNS TYPE`;
            }
            return `${line}: (:)\n DECLARE name : STRING`;
        }
        
        if (expectedType === 'KEYWORD' && expectedValue) {
            if (expectedValue === 'THEN') {
                return `${line}: IFTHEN\nIFTHEN IF x > 0 THEN`;
            }
            if (expectedValue === 'ENDIF') {
                return `${line}: IFENDIF\nIFENDIF`;
            }
            if (expectedValue === 'ENDWHILE') {
                return `${line}: WHILEENDWHILE\nWHILEENDWHILE`;
            }
            if (expectedValue === 'NEXT') {
                return `${line}: FORNEXT\nFORNEXT`;
            }
            return `${line}:  ${expectedValue}\n`;
        }
        
        if (expectedType === 'IDENTIFIER') {
            return `${line}: \n`;
        }
        
        if (expectedType === 'ASSIGN') {
            return `${line}: ()\n   x  10`;
        }
        
        // 
        const expected = expectedValue ? `${expectedValue}` : this.getTokenTypeName(expectedType);
        const actual = token ? token.value : '';
        return `${line}:  ${expected} ${actual}\n`;
    }
    
    // token
    getTokenTypeName(type) {
        const names = {
            'KEYWORD': '',
            'IDENTIFIER': '',
            'COLON': '(:)',
            'SEMICOLON': '(;)',
            'COMMA': '(,)',
            'LPAREN': '((',
            'RPAREN': '))',
            'ASSIGN': '()',
            'EOF': ''
        };
        return names[type] || type;
    }
    
    // 
    getFriendlyErrorMessage(token) {
        const tokenType = token.type;
        const tokenValue = token.value;
        
        if (tokenType === 'COLON') {
            return `${token.line}: (:)\n DECLARE name : STRING\nDECLAREIFFOR`;
        }
        
        if (tokenType === 'SEMICOLON') {
            return `${token.line}: (;)\n`;
        }
        
        if (tokenType === 'COMMA') {
            return `${token.line}: (,)\n`;
        }
        
        if (tokenType === 'RPAREN' || tokenType === 'RBRACKET') {
            return `${token.line}: ${tokenType === 'RPAREN' ? ')' : ']'}\n`;
        }
        
        // 
        return `${token.line}:  ${tokenValue}\nDECLAREIFFORWHILE`;
    }
    
    // 
    addError(message, line = null, column = null) {
        const token = this.currentToken();
        this.errors.push({
            type: 'SYNTAX_ERROR',
            message,
            line: line || (token ? token.line : 0),
            column: column || (token ? token.column : 0)
        });
    }
}

// Node.js
// ES6 
export default PseudocodeParser;

// 
if (typeof window !== 'undefined') {
    window.PseudocodeParser = PseudocodeParser;
}