// LLM-VL
// 

class LLMVLIntegration {
    constructor() {
        this.apiEndpoint = process.env.LLM_VL_API_ENDPOINT || 'https://api.openai.com/v1/chat/completions';
        this.apiKey = process.env.LLM_VL_API_KEY || '';
        this.model = 'gpt-4-vision-preview';
    }

    /**
     * LLM-VL
     * @param {string} input - URL
     * @param {string} type - 'text'  'image'
     * @returns {Promise<Object>} 
     */
    async analyzePseudocode(input, type = 'text') {
        try {
            const prompt = this.buildPrompt(type);
            const messages = this.buildMessages(prompt, input, type);
            
            const response = await this.callLLMVL(messages);
            return this.parseResponse(response);
            
        } catch (error) {
            console.error('LLM-VL:', error);
            return {
                success: false,
                error: error.message,
                suggestions: []
            };
        }
    }

    /**
     * 
     * @param {string} type - 
     * @returns {string} 
     */
    buildPrompt(type) {
        const basePrompt = `Cambridge 9618${type === 'image' ? '' : ''}

1. 
2.    <  =
3. IF, FOR, WHILE, REPEAT
4. 
5. 
6. 


- 
- 
- 
- 

JSON
{
  "errors": [
    {
      "line": ,
      "type": "",
      "message": "",
      "suggestion": "",
      "corrected": ""
    }
  ],
  "corrected_code": "",
  "confidence": 0.95
}`;

        return basePrompt;
    }

    /**
     * API
     * @param {string} prompt - 
     * @param {string} input - 
     * @param {string} type - 
     * @returns {Array} 
     */
    buildMessages(prompt, input, type) {
        const messages = [
            {
                role: 'system',
                content: prompt
            }
        ];

        if (type === 'text') {
            messages.push({
                role: 'user',
                content: `\n\n${input}`
            });
        } else if (type === 'image') {
            messages.push({
                role: 'user',
                content: [
                    {
                        type: 'text',
                        text: ''
                    },
                    {
                        type: 'image_url',
                        image_url: {
                            url: input
                        }
                    }
                ]
            });
        }

        return messages;
    }

    /**
     * LLM-VL API
     * @param {Array} messages - 
     * @returns {Promise<Object>} API
     */
    async callLLMVL(messages) {
        const response = await fetch(this.apiEndpoint, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${this.apiKey}`
            },
            body: JSON.stringify({
                model: this.model,
                messages: messages,
                max_tokens: 2000,
                temperature: 0.1
            })
        });

        if (!response.ok) {
            throw new Error(`API: ${response.status} ${response.statusText}`);
        }

        return await response.json();
    }

    /**
     * API
     * @param {Object} response - API
     * @returns {Object} 
     */
    parseResponse(response) {
        try {
            const content = response.choices[0].message.content;
            const jsonMatch = content.match(/\{[\s\S]*\}/);
            
            if (jsonMatch) {
                const result = JSON.parse(jsonMatch[0]);
                return {
                    success: true,
                    ...result
                };
            } else {
                return {
                    success: false,
                    error: '',
                    raw_response: content
                };
            }
        } catch (error) {
            return {
                success: false,
                error: '',
                raw_response: response
            };
        }
    }

    /**
     * 
     * @param {Array} files - 
     * @returns {Promise<Array>} 
     */
    async batchAnalyze(files) {
        const results = [];
        
        // Node.js
        if (typeof window !== 'undefined') {
            console.warn('batchAnalyze is not supported in browser environment');
            return [];
        }
        
        // fswebpack
        let fs;
        try {
            fs = eval('require')('fs');
        } catch (e) {
            console.warn('fs module not available');
            return [];
        }
        
        for (const file of files) {
            try {
                const content = fs.readFileSync(file, 'utf8');
                const result = await this.analyzePseudocode(content, 'text');
                
                results.push({
                    file: file,
                    ...result
                });
                
                // API
                await new Promise(resolve => setTimeout(resolve, 1000));
                
            } catch (error) {
                results.push({
                    file: file,
                    success: false,
                    error: error.message
                });
            }
        }
        
        return results;
    }

    /**
     * 
     * @param {Array} results - 
     * @returns {string} HTML
     */
    generateReport(results) {
        let html = `
        <!DOCTYPE html>
        <html>
        <head>
            <title></title>
            <style>
                body { font-family: Arial, sans-serif; margin: 20px; }
                .file-section { margin-bottom: 30px; border: 1px solid #ddd; padding: 15px; }
                .error { color: red; margin: 5px 0; }
                .suggestion { color: green; margin: 5px 0; }
                .corrected-code { background: #f5f5f5; padding: 10px; margin: 10px 0; }
                .confidence { font-weight: bold; color: blue; }
            </style>
        </head>
        <body>
            <h1></h1>
            <p>: ${new Date().toLocaleString()}</p>
        `;

        for (const result of results) {
            html += `
            <div class="file-section">
                <h2>: ${result.file}</h2>
            `;

            if (result.success) {
                html += `<p class="confidence">: ${(result.confidence * 100).toFixed(1)}%</p>`;
                
                if (result.errors && result.errors.length > 0) {
                    html += '<h3>:</h3>';
                    for (const error of result.errors) {
                        html += `
                        <div class="error">
                            <strong>${error.line} - ${error.type}:</strong> ${error.message}<br>
                            <span class="suggestion">: ${error.suggestion}</span><br>
                            <code>${error.corrected}</code>
                        </div>
                        `;
                    }
                } else {
                    html += '<p style="color: green;"></p>';
                }

                if (result.corrected_code) {
                    html += `
                    <h3>:</h3>
                    <pre class="corrected-code">${result.corrected_code}</pre>
                    `;
                }
            } else {
                html += `<p class="error">: ${result.error}</p>`;
            }

            html += '</div>';
        }

        html += `
        </body>
        </html>
        `;

        return html;
    }
}

// 
// ES6 
export default LLMVLIntegration;

// 
if (typeof window !== 'undefined') {
    window.LLMVLIntegration = LLMVLIntegration;
}

// 
if (require.main === module) {
    const integration = new LLMVLIntegration();
    
    // 
    const sampleCode = `
    FOR i < 1 TO 10
        sum < sum + i
    NEXT i
    `;
    
    integration.analyzePseudocode(sampleCode, 'text')
        .then(result => {
            console.log(':', JSON.stringify(result, null, 2));
        })
        .catch(error => {
            console.error(':', error);
        });
}