/**
 * Pseudocode 
 *  Cambridge 9618 
 */

class PseudocodeLexer {
    constructor() {
        // 
        this.keywords = new Set([
            // 
            'INTEGER', 'REAL', 'CHAR', 'STRING', 'BOOLEAN', 'DATE',
            // 
            'DECLARE', 'CONSTANT', 'TYPE', 'ENDTYPE',
            // 
            'IF', 'THEN', 'ELSE', 'ELSEIF', 'ENDIF',
            'CASE', 'OF', 'OTHERWISE', 'ENDCASE',
            // 
            'FOR', 'TO', 'STEP', 'NEXT', 'WHILE', 'ENDWHILE',
            'REPEAT', 'UNTIL',
            // 
            'PROCEDURE', 'ENDPROCEDURE', 'FUNCTION', 'ENDFUNCTION',
            'RETURNS', 'RETURN', 'BYVAL', 'BYREF', 'CALL',
            // 
            'INPUT', 'OUTPUT',
            // 
            'OPENFILE', 'READFILE', 'WRITEFILE', 'CLOSEFILE',
            'EOF', 'SEEK', 'GETPOSITION',
            // 
            'AND', 'OR', 'NOT',
            // 
            'TRUE', 'FALSE',
            // 
            'DIV', 'MOD', 'ARRAY',
            // 
            'ASC', 'CHR', 'INT', 'RAND'
        ]);

        // 
        this.operators = {
            '': 'ASSIGN',
            '<-': 'ASSIGN',
            '=': 'EQUALS',
            '<>': 'NOT_EQUALS',
            '!=': 'NOT_EQUALS',
            '<': 'LESS_THAN',
            '>': 'GREATER_THAN',
            '<=': 'LESS_EQUAL',
            '>=': 'GREATER_EQUAL',
            '+': 'PLUS',
            '-': 'MINUS',
            '*': 'MULTIPLY',
            '/': 'DIVIDE',
            '(': 'LPAREN',
            ')': 'RPAREN',
            '[': 'LBRACKET',
            ']': 'RBRACKET',
            ',': 'COMMA',
            ':': 'COLON',
            ';': 'SEMICOLON',
            '.': 'DOT'
        };

        this.reset();
    }

    reset() {
        this.input = '';
        this.position = 0;
        this.line = 1;
        this.column = 1;
        this.tokens = [];
        this.errors = [];
    }

    tokenize(input) {
        this.reset();
        this.input = input;

        while (this.position < this.input.length) {
            const char = this.currentChar();

            // 
            if (/\s/.test(char)) {
                this.skipWhitespace();
                continue;
            }

            // 
            if (char === '/' && this.peek() === '/') {
                this.skipComment();
                continue;
            }

            // 
            if (char === '"') {
                this.readString();
                continue;
            }

            // 
            if (char === "'") {
                this.readChar();
                continue;
            }

            // 
            if (this.isDigit(char)) {
                this.readNumber();
                continue;
            }

            // 
            if (this.isLetter(char) || char === '_') {
                this.readIdentifier();
                continue;
            }

            // 
            const twoChar = char + this.peek();
            if (this.operators[twoChar]) {
                this.addToken(this.operators[twoChar], twoChar);
                this.advance();
                this.advance();
                continue;
            }

            // 
            if (this.operators[char]) {
                this.addToken(this.operators[char], char);
                this.advance();
                continue;
            }

            // 
            this.addError(`: '${char}'`);
            this.advance();
        }

        // 
        this.addToken('EOF', null);
        return {
            tokens: this.tokens,
            errors: this.errors
        };
    }

    currentChar() {
        return this.input[this.position] || null;
    }

    peek(offset = 1) {
        return this.input[this.position + offset] || null;
    }

    advance() {
        if (this.position < this.input.length) {
            if (this.currentChar() === '\n') {
                this.line++;
                this.column = 1;
            } else {
                this.column++;
            }
            this.position++;
        }
    }

    skipWhitespace() {
        while (this.position < this.input.length && /\s/.test(this.currentChar())) {
            this.advance();
        }
    }

    skipComment() {
        //  // 
        while (this.position < this.input.length && this.currentChar() !== '\n') {
            this.advance();
        }
        if (this.currentChar() === '\n') {
            this.advance();
        }
    }

    readString() {
        const startLine = this.line;
        const startColumn = this.column;
        let value = '';
        
        this.advance(); // 
        
        while (this.position < this.input.length && this.currentChar() !== '"') {
            const char = this.currentChar();
            if (char === '\n') {
                this.addError('', startLine, startColumn);
                return;
            }
            if (char === '\\') {
                this.advance();
                const escaped = this.currentChar();
                if (escaped === 'n') value += '\n';
                else if (escaped === 't') value += '\t';
                else if (escaped === 'r') value += '\r';
                else if (escaped === '\\') value += '\\';
                else if (escaped === '"') value += '"';
                else value += escaped;
            } else {
                value += char;
            }
            this.advance();
        }
        
        if (this.currentChar() !== '"') {
            this.addError('', startLine, startColumn);
            return;
        }
        
        this.advance(); // 
        this.addToken('STRING_LITERAL', value, startLine, startColumn);
    }

    readChar() {
        const startLine = this.line;
        const startColumn = this.column;
        let value = '';
        
        this.advance(); // 
        
        if (this.currentChar() === "'") {
            this.addError('', startLine, startColumn);
            this.advance();
            return;
        }
        
        while (this.position < this.input.length && this.currentChar() !== "'") {
            const char = this.currentChar();
            if (char === '\n') {
                this.addError('', startLine, startColumn);
                return;
            }
            
            // 
            if (char === '\\') {
                this.advance();
                const escaped = this.currentChar();
                if (escaped === "'") {
                    value += "'";
                } else if (escaped === '\\') {
                    value += '\\';
                } else if (escaped === 'n') {
                    value += '\n';
                } else if (escaped === 't') {
                    value += '\t';
                } else if (escaped === 'r') {
                    value += '\r';
                } else {
                    value += escaped || '';
                }
                this.advance();
            } else {
                value += char;
                this.advance();
            }
        }
        
        if (this.currentChar() !== "'") {
            this.addError('', startLine, startColumn);
            return;
        }
        
        if (value.length > 1) {
            this.addError('', startLine, startColumn);
        }
        
        this.advance(); // 
        this.addToken('CHAR_LITERAL', value, startLine, startColumn);
    }

    readNumber() {
        const startLine = this.line;
        const startColumn = this.column;
        let value = '';
        let hasDecimal = false;
        
        while (this.position < this.input.length && 
               (this.isDigit(this.currentChar()) || this.currentChar() === '.')) {
            if (this.currentChar() === '.') {
                if (hasDecimal) {
                    this.addError('', startLine, startColumn);
                    break;
                }
                hasDecimal = true;
            }
            value += this.currentChar();
            this.advance();
        }
        
        const tokenType = hasDecimal ? 'REAL_LITERAL' : 'INTEGER_LITERAL';
        this.addToken(tokenType, value, startLine, startColumn);
    }

    readIdentifier() {
        const startLine = this.line;
        const startColumn = this.column;
        let value = '';
        
        while (this.position < this.input.length && 
               (this.isLetter(this.currentChar()) || 
                this.isDigit(this.currentChar()) || 
                this.currentChar() === '_')) {
            value += this.currentChar();
            this.advance();
        }
        
        const tokenType = this.keywords.has(value.toUpperCase()) ? 'KEYWORD' : 'IDENTIFIER';
        // Keep original case for identifiers, only uppercase for keywords
        const tokenValue = tokenType === 'KEYWORD' ? value.toUpperCase() : value;
        this.addToken(tokenType, tokenValue, startLine, startColumn);
    }

    isDigit(char) {
        return char && /[0-9]/.test(char);
    }

    isLetter(char) {
        return char && /[a-zA-Z]/.test(char);
    }

    addToken(type, value, line = this.line, column = this.column) {
        this.tokens.push({
            type,
            value,
            line,
            column
        });
    }

    addError(message, line = this.line, column = this.column) {
        this.errors.push({
            message,
            line,
            column,
            type: 'lexical'
        });
    }

    getTokenTypeName(type) {
        const typeNames = {
            'KEYWORD': '',
            'IDENTIFIER': '',
            'INTEGER_LITERAL': '',
            'REAL_LITERAL': '',
            'STRING_LITERAL': '',
            'CHAR_LITERAL': '',
            'ASSIGN': '',
            'EQUALS': '',
            'NOT_EQUALS': '',
            'LESS_THAN': '',
            'GREATER_THAN': '',
            'LESS_EQUAL': '',
            'GREATER_EQUAL': '',
            'PLUS': '',
            'MINUS': '',
            'MULTIPLY': '',
            'DIVIDE': '',
            'LPAREN': '',
            'RPAREN': '',
            'LBRACKET': '',
            'RBRACKET': '',
            'COMMA': '',
            'COLON': '',
            'SEMICOLON': '',
            'DOT': '',
            'EOF': ''
        };
        return typeNames[type] || type;
    }
}

// Node.js
// ES6 
export default PseudocodeLexer;

// 
if (typeof window !== 'undefined') {
    window.PseudocodeLexer = PseudocodeLexer;
}